	import Home from '@/components/rf-back-home/rf-back-home';
  export default {
    components: {Home},
    data() {
      return {
        userInfo: {},
        loading: true
      }
    },
    onLoad() {
      this.initData();
    },
    methods: {
      // 初始化数据
      initData() {
        this.userInfo = uni.getStorageSync('userInfo') || undefined;
        this.loading = false;
      },
      navTo(url){
        uni.navigateTo({
          url
        })
      },
    }
  }
